import React, { useEffect, useState } from 'react';
import { styled } from '@mui/material/styles';
import { Box } from '@mui/material';
import { toast } from 'react-toastify';
import io from 'socket.io-client';
import ChatInterface from './ChatInterface';

const MainContainer = styled(Box)(({ theme }) => ({
  minHeight: '100vh',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  background: 'linear-gradient(180deg, #1a1a2e 0%, #16162c 100%)',
}));

const VoiceChat = () => {
  const [socket, setSocket] = useState(null);
  const [isInCall, setIsInCall] = useState(false);
  const [micStatus, setMicStatus] = useState({ active: false, stream: null });

  useEffect(() => {
    const newSocket = io('https://airspeak.onrender.com', {
      transports: ['polling', 'websocket'],
      secure: true,
      rejectUnauthorized: false,
      withCredentials: true,
      autoConnect: true,
      reconnection: true,
      reconnectionAttempts: 10,
      reconnectionDelay: 1000
    });

    newSocket.on('connect', () => {
      console.log('Connected to server with ID:', newSocket.id);
      newSocket.emit('join', {});
    });

    newSocket.on('connect_error', (error) => {
      console.error('Socket connection error:', error);
      toast.error('Could not connect to server. Please try again later.');
    });

    // Request microphone access
    navigator.mediaDevices.getUserMedia({ audio: true })
      .then(stream => {
        setMicStatus({ active: true, stream });
        toast.success('Microphone access granted');
      })
      .catch(err => {
        console.error('Microphone error:', err);
        setMicStatus({ active: false, stream: null });
        toast.error('Microphone access is required. Please allow microphone access and refresh the page.');
      });

    setSocket(newSocket);

    return () => {
      if (micStatus.stream) {
        micStatus.stream.getTracks().forEach(track => track.stop());
      }
      newSocket.close();
    };
  }, []);

  return (
    <MainContainer>
      <ChatInterface 
        micStatus={micStatus}
        socket={socket}
        isInCall={isInCall}
        setIsInCall={setIsInCall}
      />
    </MainContainer>
  );
};

export default VoiceChat;
